import React from 'react';
import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from 'react-responsive-carousel';

const vocationalTraining = (props) => {

    const training = (
        <div>
            <Carousel
                infiniteLoop
                centerMode
                showThumbs={false}
                showStatus={false}
                interval={2500}
                transitionTime={1000}
                className="carousel"
                >
                {props.certificate.map((training) =>
                    <div className='image' key={training.name}>
                        <img src={training.url} alt={training.name} />
                        <p className="legend"> { training.name } </p>
                    </div>
                )}
            </Carousel>
        </div>
    );
    return (
        <div className='title'>
            <i className='fa fa-trophy'></i>
            <h2>VOCATIONAL TRAINING</h2>
            {training}
        </div>
    )
};


export default vocationalTraining;



